html {
  height: 100%;
  overflow-x: hidden;
}

body {
  position: relative;
  display: flex;
  min-height: 100%;
  margin: 0;
  flex-direction: column;
  font-family: $Oswald;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: $Tundora;
  background-color: $White;
  overflow-x: hidden;
}
